<template>
  <div class="newsinfo">
    <h3 class="title">{{newsInfocon.title}}</h3>
    <p class="title-msg">
      <span>发表时间:{{newsInfocon.add_time|dateForat}}</span>
      <span>点击: {{newsInfocon.click}}次</span>
    </p>
    <hr>
    <div class="newContent" v-html="newsInfocon.content">
       
    </div>

    <!-- 评论区 -->
    <comment-box :infoid="this.id"></comment-box>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import comment from '../childcomponents/Comment'

export default {
  data() {
    return {
      id: this.$route.params.id,
      newsInfocon:{}
    };
  },
  methods: {
    getNewsInfo() {
      this.$axios
        .get("http://www.liulongbin.top:3005/api/getnew/" + this.id)
        .then(res => {
          if (res.data.status == 0) {
            this.newsInfocon = res.data.message[0];  
          } else {
            Toast("获取数据失败");
          }
        });
    }
  },
  created() {
    this.getNewsInfo();
  },
   components:{
    'comment-box':comment
  }
};
</script>

<style lang="less" scoped>
.newsinfo {
  padding: 0 5px;
  .title {
    text-align: center;
    font-size: 14px;
    color: red;
    margin: 15px 0;
  }
  .title-msg {
    font-size: 13px;
    color: #226aff;
    display: flex;
    justify-content: space-between;
  }
  .newContent{
      img{
          width: 100%;
      }
  }
}
</style>

