<template>
  <div class="nb">
    <div class="mui-numbox" data-numbox-min="1" data-numbox-max="maxNum">
      <button class="mui-btn mui-btn-numbox-minus" type="button">-</button>
      <input id="test" class="mui-input-numbox" type="number" :value="1" @change="getVal" ref="numBox">
      <button class="mui-btn mui-btn-numbox-plus" type="button" disabled>+</button>
    </div>
  </div>
</template>

<script>
import mui from "../../lib/mui/js/mui.js";
export default {
  data() {
    return {};
  },
  mounted() {
    mui(".mui-numbox").numbox();
  },
  methods: {
    getVal() {
      let num = parseInt(this.$refs.numBox.value);
      this.$emit("getNum", num);
    }
  },
  props: ["maxNum"],
  watch: {
    maxNum: newVal => {
      mui(".mui-numbox")
        .numbox()
        .setOption("max", newVal);
    }
  }
};
</script>

<style lang="less">
.nb {
  display: inline-block;
  margin: 0 10px;
  .mui-numbox{
    height:28px;
  }
}
</style>
