<template>
  <div class="home">
    <!-- 轮播图 -->
    <swiper :lunbotuList="lunbotuList" :full="true"></swiper>
    <!-- 六宫格 -->
    <ul class="mui-table-view mui-grid-view mui-grid-9">
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <router-link to="/home/newslist">
          <img src="@/assets/img/menu1.png" alt="">
          <div class="mui-media-body">新闻资讯</div>
        </router-link>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <router-link to="/home/photoslist">
          <img src="@/assets/img/menu2.png" alt="">
          <div class="mui-media-body">图片分享</div>
        </router-link>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <router-link to="/home/goodslist">
          <img src="@/assets/img/menu3.png" alt="">
          <div class="mui-media-body">商品购买</div>
        </router-link>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <a href="#">
          <img src="@/assets/img/menu4.png" alt="">
          <div class="mui-media-body">留言反馈</div>
        </a>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <a href="#">
          <img src="@/assets/img/menu5.png" alt="">
          <div class="mui-media-body">视频专区</div>
        </a>
      </li>
      <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3">
        <a href="#">
          <img src="@/assets/img/menu6.png" alt="">
          <div class="mui-media-body">联系我们</div>
        </a>
      </li>
    </ul>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
import Swiper from '../../views/swipe/Swiper'

export default {
  data() {
    return {
      lunbotuList: []
    };
  },
  methods: {
    getImg() {
      this.$axios
        .get("http://www.liulongbin.top:3005/api/getlunbo")
        .then(res => {
          let data = res.data;
          if (data.status === 0) {
            this.lunbotuList = data.message;
          } else {
            Toast({
              message: "获取数据失败",
              position: "bottom",
              duration: 5000
            });
          }
        });
    }
  },
  created() {
    this.getImg();
  },
  components:{
    'swiper':Swiper
  }
};
</script>

<style lang="less" scoped>
.home {
 
  .mui-grid-view.mui-grid-9{
      background-color: #fff;
      border:0;
      .mui-table-view-cell{
          border:0;
      }
      img{
          width: 50%;
      }
  }
  .mui-table-view.mui-grid-view .mui-table-view-cell .mui-media-body{
      font-size: 13px;
  }
}
</style>
