import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
let car = localStorage.car || '[]'
car = JSON.parse(car)

export default new Vuex.Store({
  state: {
    car//购物车中的数据 存储每一个商品的对象｛id:1,count:数量,price:价格,selected:是否选中｝
  },
  mutations: {
    addGoods(state, data) {//实现加入购物车

      let flag = false;//默认没找到
      //如果有对应的id只要更新数量
      state.car.some(item => {
        if (item.id == data.id) {

          item.count += parseInt(data.count)
          flag = true
          return true
        }
      })
      if (!flag) {
        state.car.push(data)
      }
      localStorage.car = JSON.stringify(state.car)
      
    },
    updateGoods(state,goosinfo){//实现数量一致
      
      state.car.some(item=>{
        if(item.id==goosinfo.id){

          item.count=goosinfo.count
          return true
        }
        localStorage.car = JSON.stringify(state.car)
      })

    },
    removeGoods(state,id){
      state.car.some((ele,i)=>{
        if(ele.id==id){
          state.car.splice(i,1)
        }
      })
      localStorage.car = JSON.stringify(state.car)
    },
    updateSelected(state,id){
      state.car.some(item=>{
        if(item.id==id){
          item.selected=!item.selected
          return true
        }
      })
      localStorage.car = JSON.stringify(state.car)
    }
  },
  actions: {

  },
  getters: {
    getAllCount(state) {
      let c = 0
      // console.log(state.car);
      
      state.car.forEach(item => {
        c += item.count
      })
      return c
    },
    getCount(state){//实现购物车页面商品数量
      //设置一个对象存
      let countObj={}
      state.car.forEach(ele=>{
        countObj[ele.id]=ele.count
      })
      return countObj
    },
    getGoodsSelected(state){//实现获取商品的选中状态
      let selectedObj={}
      state.car.forEach(item=>{
        selectedObj[item.id]=item.selected
      })
      return selectedObj
    },
    getGoodsAggregate(state){//实现商品数量价格的
      let Goods={
        count:0,
        totalPrice:0
      }
      state.car.forEach(item=>{
        if(item.selected){
          Goods.count+=item.count
          Goods.totalPrice+=(item.count)*item.price
        }
      })
      // console.log(Goods)
      return Goods
    }
  }
})
