<template>
  <div class="my-cart">
    <div class="mui-card" v-for="(item,i) in cartList" :key="item.id">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <mt-switch v-model="$store.getters.getGoodsSelected[item.id]" @change="selectedChange(item.id)"></mt-switch>
          <img :src="item.thumb_path" alt>
          <div class="info">
            <h3>{{item.title}}</h3>
            <div>
              <span class="price">￥{{item.sell_price}}</span>
              <numbox :cartCount="$store.getters.getCount[item.id]" :goodsId="item.id"></numbox>
              <a href="#" @click.prevent="romve(item.id,i)">删除</a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner my-jiesuan">
          <div class="settle-accounts-left">
            <p>总计(不含运费)</p>
            <p>已勾选商品 <span class="num">{{$store.getters.getGoodsAggregate.count}}</span> 件, 总价<span class="total-price">￥{{$store.getters.getGoodsAggregate.totalPrice}}</span></p>
          </div>
          <button type="button" class="mui-btn mui-btn-danger">去结算</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import numbox from "../../views/numbox/shop-numbox";
export default {
  data() {
    return {
      cartList: []
    };
  },
  methods: {
    getCartInfo() {
      let cartId = [];

      if (!localStorage.car) {
        return;
      }
      //获取商品id
      JSON.parse(localStorage.car).forEach(ele => {
        cartId.push(ele.id);
      });
      if (cartId.length <= 0) {
        return;
      }

      //发送请求获取商品
      this.$axios
        .get(
          "http://www.liulongbin.top:3005/api/goods/getshopcarlist/" +
            cartId.join(",")
        )
        .then(res => {
          if (res.data.status === 0) {
            this.cartList = res.data.message;
            // console.log(this.cartList);
          }
        });
    },
    romve(id,index){
      this.cartList.splice(index,1)
      //还要修改store里的
      this.$store.commit('removeGoods',id)
    },
    selectedChange(id){//实现状态改变
      this.$store.commit('updateSelected',id)
      this.$store.getters.getGoodsAggregate
    }
  },
  created() {
    this.getCartInfo();
  },
  components: {
    numbox
  }
};
</script>

<style lang="less">
.my-cart {
  background-color: #eee;
  overflow: hidden;
  .mui-card-content-inner {
    display: flex;
    align-items: center;
    img {
      width: 50px;
      height: 50px;
    }
    .info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      h3 {
        font-size: 13px;
        text-align: center;
      }
      .price {
        color: red;
      }
    }
    &.my-jiesuan{
      justify-content:space-between;
    }
    .num,.total-price{
      color: red;
    }
  }
}
</style>
