<template>
  <div class="goodsinfo">
    <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>

    <!-- 轮播图区 -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <swiper :lunbotuList="goodsImgList" :full="false"></swiper>
        </div>
      </div>
    </div>
    <!-- 手机购买区域 -->
    <div class="mui-card">
      <div class="mui-card-header">{{goodsInfo.title}}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner my-body">
          <p class="price">
            市场价:
            <del>￥{{goodsInfo.market_price}}</del>&nbsp;&nbsp;销售价:
            <span class="new_price">￥{{goodsInfo.sell_price}}</span>
          </p>
          <div class="my-numbox">购买数量:
            <numbox @getNum="getComNum" :maxNum="goodsInfo.stock_quantity"></numbox>
            <!-- <div class="mui-numbox" data-numbox-min="1" data-numbox-max="9">
					<button class="mui-btn mui-btn-numbox-minus" type="button">-</button>
					<input id="test" class="mui-input-numbox" type="number" value="5">
					<button class="mui-btn mui-btn-numbox-plus" type="button" disabled="">+</button>
            </div>-->
          </div>
          <p>
            <mt-button type="primary" size="small" class="buy">立即购买</mt-button>
            <mt-button type="danger" size="small" @click="addToShopCart">加入购物车</mt-button>
          </p>
        </div>
      </div>
    </div>
    <!-- 详情区域 -->
    <div class="mui-card">
      <div class="mui-card-header">商品参数</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>商品货号: {{goodsInfo.goods_no}}</p>
          <p>库存情况: {{goodsInfo.stock_quantity}} 件</p>
          <p>上架时间: {{goodsInfo.add_time|dateForat}}</p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button
          type="primary"
          size="large"
          plain
          class="my-presentation"
          @click="goDesc(id)"
        >图文介绍</mt-button>
        <mt-button type="danger" size="large" plain @click="goComment(id)">商品品论</mt-button>
      </div>
    </div>
  </div>
</template>
<script>
import Swipe from "../swipe/Swiper";
import NumBox from "../numbox/NumBoxs";

export default {
  data() {
    return {
      goodsImgList: [],
      id: this.$route.params.id,
      goodsInfo: {},
      ballFlag: false,
      comNum: 1
    };
  },
  methods: {
    getGoodsImg() {
      //获取商品图片
      this.$axios
        .get("http://www.liulongbin.top:3005/api/getthumimages/" + this.id)
        .then(res => {
          //   console.log(res);
          if (res.data.status === 0) {
            res.data.message.forEach(ele => {
              ele.img = ele.src;
            });
            this.goodsImgList = res.data.message;
          }
        });
    },
    getGoodsInfo() {
      //获取图文详情
      this.$axios
        .get("http://www.liulongbin.top:3005/api/goods/getinfo/" + this.id)
        .then(res => {
          // console.log(res);
          if (res.data.status === 0) {
            this.goodsInfo = res.data.message[0];

            // console.log(this.goodsInfo)
          }
        });
    },
    goDesc(id) {
      //获取图文介绍
      this.$router.push({ name: "gdesc", params: { id } });
    },
    goComment(id) {
      //商品品论
      this.$router.push({ name: "gcmt", params: { id } });
    },
    addToShopCart() {
      //加入购物车
      this.ballFlag = !this.ballFlag;
      let goods = {
        id: this.id,
        count: parseInt(this.comNum),
        price: this.goodsInfo.sell_price,
        selected: true
      };
      //实现数据存到vuex中
      this.$store.commit("addGoods", goods);
      // console.log(this.$store.state.car)
    },
    beforeEnter(ele) {
      ele.style.transform = "translate(0,0)";
    },
    enter(ele, done) {
      ele.offsetWidth;

      const ball = this.$refs.ball.getBoundingClientRect();
      const comNum = document.querySelector("#comNum").getBoundingClientRect();
      let x = Math.abs(ball.left - comNum.left);
      let y = Math.abs(ball.top - comNum.top);

      ele.style.transform = "translate(" + x + "px," + y + "px)";
      ele.style.transition = "all 1s cubic-bezier(.38,-0.3,.92,.63)";
      done();
    },
    afterEnter() {
      this.ballFlag = !this.ballFlag;
    },
    getComNum(comNum) {
      this.comNum = comNum;
    }
  },
  created() {
    this.getGoodsImg();
    this.getGoodsInfo(); //获取图文详情
  },
  components: {
    swiper: Swipe,
    numbox: NumBox
  }
};
</script>
<style lang="less" scoped>
.goodsinfo {
  background-color: #eee;
  overflow: hidden;
  .my-body {
    font-size: 13px;
    .price {
      font-size: 13px;
      .new_price {
        color: red;
      }
    }
    .my-numbox {
      margin: 10px 0;
    }
    .buy {
      margin-right: 10px;
    }
  }
  .mui-card-footer {
    display: block;
  }
  .my-presentation {
    margin-bottom: 10px;
  }
  .ball {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: red;
    position: absolute;
    left: 138px;
    top: 389px;
    z-index: 999;
  }
}
</style>
