<template>
  <div id="newsList">
    <ul class="mui-table-view">
      <li class="mui-table-view-cell mui-media" v-for="item in newsList" :key="item.id">
        <router-link :to="'/home/newsinfo/'+item.id">
          <img
            class="mui-media-object mui-pull-left"
            :src="item.img_url"
          >
          <div class="mui-media-body">
            <h2 class="mui-ellipsis">{{item.title}}</h2>
            <p class="mui-ellipsis newsMsg">
              <span>发表时间: {{item.add_time|dateForat}}</span>
              <span>点击: {{item.click}}次</span>
            </p>
          </div>
        </router-link>
      </li>
     
    </ul>
  </div>
</template>
<script>
import { Toast } from "mint-ui";


export default {
  data() {
    return {
      newsList:[]
    };
  },
  methods: {
    getNewsList() {
      this.$axios
        .get("http://www.liulongbin.top:3005/api/getnewslist")
        .then(res => {
          if(res.data.status==0){
            this.newsList=res.data.message
           
          }else{
            Toast('获取数据失败')
          }
        });
      
    }
  },
  created() {
    this.getNewsList()
  },
 
};
</script>
<style lang="less" scoped>

#newsList{
  height: 100%;
}
.mui-table-view {
  li {
    h2 {
      font-size: 14px;
      font-weight: 400;
    }
    .newsMsg {
      font-size: 12px;
      color: #226aff;
      display: flex;
      justify-content: space-between;
    }
  }
}
</style>
