<template>
  <div class="goods-list">
    <div class="goods-item" v-for='item in goodsList' :key="item.id" @click="skip(item.id)">
      <div class="goos-title">
        <img :src="item.img_url" alt>
        <h3 class="goos-name mui-ellipsis-2">{{item.title}}</h3>
      </div>
      <div class="goods-body">
        <div class="goods-price">
          <span class="newprice">￥{{item.sell_price}}</span>
          <span class="oldprice">￥{{item.market_price}}</span>
        </div>
        <div class="goods-num">
          <span>热卖中</span>
          <span>剩{{item.stock_quantity}}件</span>
        </div>
      </div>
    </div>
    <mt-button type="danger" size="large" @click="addGoods">加载更多</mt-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      goodsList: [],
      pageindex:1
    };
  },
  methods: {
    getGoodsList() {
      this.$axios
        .get("http://www.liulongbin.top:3005/api/getgoods?pageindex="+this.pageindex)
        .then(res => {
        //   console.log(res);
          if (res.data.status === 0) {
            this.goodsList = this.goodsList.concat(res.data.message);
          }
        });
    },
    addGoods(){//加载更多
        this.pageindex++
        this.getGoodsList()
    },
    skip(id){//实现路由跳转
        this.$router.push('/home/goodsinfo/'+id)
        // this.$router.go(-1)
    }
  },
  created() {
    this.getGoodsList();
  }
};
</script>



<style lang="less" scoped>
.goods-list {
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .goods-item {
    width: 49%;
    margin-bottom: 10px;
    min-height: 266px;
    // background-color: pink;
    box-shadow: 0 0 5px #ccc;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .goos-title {
      background-color: #fff;
      padding: 5px;
    //   height: 210px;

      img {
        width: 100%;
        // height: 170px;
        display: block;
      }
      h3 {
        font-size: 13px;
        margin: 0;
        padding: 5px 0;
        font-weight: 500;
      }
    }
    .goods-body {
      background-color: #ddd;
      padding: 5px;
      .goods-price {
        .newprice {
          color: red;
          font-size: 16px;
          margin-right: 10px;
        }
        .oldprice {
          font-size: 13px;
          color: #999;
          text-decoration: line-through;
        }
      }
      .goods-num {
        display: flex;
        justify-content: space-between;
        font-size: 13px;
        color: #888;
      }
    }
  }
}
</style>

