<template>
  <div id="app">
    <!-- 头部模块 -->
    <mt-header fixed title="这是一个项目">
      <span @click="goBack" slot="left" v-if="flag">
        <mt-button icon="back">返回</mt-button>
      </span>
    </mt-header>
    <transition>
      <router-view></router-view>
    </transition>

    <nav class="mui-bar mui-bar-tab">
      <router-link class="mui-tab-item1" to="/home">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label">首页</span>
      </router-link>
      <router-link class="mui-tab-item1" to="/member">
        <span class="mui-icon mui-icon-contact"></span>
        <span class="mui-tab-label">会员</span>
      </router-link>
      <router-link class="mui-tab-item1" to="/cart">
        <span class="mui-icon fa fa-shopping-cart">
          <span class="mui-badge" id="comNum">{{$store.getters.getAllCount}}</span>
        </span>
        <span class="mui-tab-label">购物车</span>
      </router-link>
      <router-link class="mui-tab-item1" to="/search">
        <span class="mui-icon mui-icon-search"></span>
        <span class="mui-tab-label">搜素</span>
      </router-link>
    </nav>
  </div>
</template>

<script>
export default {
  data(){
    return {
      flag:false
    }
  },
  created(){
    // console.log(this.$route.path)
    this.flag=this.$route.path=='/home'?false:true
  }
  ,
  methods:{
    goBack(){
      this.$router.go(-1)
    }
  },
  watch:{
    '$route.path':function(newVla){
      if(newVla=='/home'){
        this.flag=false
      }else{
        this.flag=true
      }
    }
  }
};
</script>


