import Vue from 'vue'
import Router from 'vue-router'

import Home from './components/tabbar/Home.vue'
import Member from './components/tabbar/Member.vue'
import Cart from './components/tabbar/Cart.vue'
import Search from './components/tabbar/Search.vue'

import NewsList from './views/news/NewsList.vue'
import NewsInfo from './views/news/NewsInfo.vue'

import PhotosList from './views/photos/PhotosList.vue'
import PhotoInfo from './views/photos/PhotoInfo.vue'

import GoodsList from './views/goods/GoodsList.vue'
import GoodsInfo from './views/goods/GoodsInfo.vue'
import GoodsDesc from './views/goods/GoodsDesc.vue'
import GoodsCommnent from './views/goods/GoodsCommnent.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [//配置路由规则
    {path:"/",redirect:'/home'},
    {path:'/home',component:Home},
    {path:'/member',component:Member},
    {path:'/cart',component:Cart},
    {path:'/search',component:Search},
    {path:'/home/newslist',component:NewsList},
    {path:'/home/newsinfo/:id',component:NewsInfo},
    {path:'/home/photoslist',component:PhotosList},
    {path:'/home/photoinfo/:id',component:PhotoInfo},
    {path:'/home/goodslist',component:GoodsList},
    {path:'/home/goodsinfo/:id',component:GoodsInfo},
    {path:'/home/goodscommnent/:id',component:GoodsCommnent,name:'gcmt'},
    {path:'/home/goodsdesc/:id',component:GoodsDesc,name:'gdesc'},
  ],
  linkActiveClass:"mui-active"
})
