<template>
  <div>
    <!-- 顶部导航 -->
    <div
      class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted"
    >
      <div class="mui-scroll">
        <a
          :class="['mui-control-item',item.id==0?'mui-active':'']"
          v-for="item in imgCategoryList"
          :key="item.id"
          @click="getImgList(item.id)"
        >{{item.title}}</a>
      </div>
    </div>
    <!-- 获取图片列表 -->
    <ul class="photo-list">
      <router-link v-for="item in imgList" :key="item.id" tag="li" :to="'/home/photoinfo/'+item.id">
        <img v-lazy="item.img_url">
        <div class="info">
          <h3 class="info-title">{{item.title}}</h3>
          <div class="info-body">{{item.zhaiyao}}</div>
        </div>
      </router-link>
    </ul>
  </div>
</template>
<script>
import mui from "../../lib/mui/js/mui.js";

export default {
  data() {
    return {
      imgCategoryList: [],
      imgList: []
    };
  },
  methods: {
    getImgCategory() {
      //获取图片分类列表数据
      this.$axios
        .get("http://www.liulongbin.top:3005/api/getimgcategory")
        .then(res => {
          if (res.data.status === 0) {
            res.data.message.unshift({ title: "全部", id: 0 });
            this.imgCategoryList = res.data.message;
            this.$nextTick(() => {});
          }
        });
    },
    getImgList(cateid) {
      //获取图片列表
      this.$axios
        .get("http://www.liulongbin.top:3005/api/getimages/" + cateid)
        .then(res => {
          if (res.data.status === 0) {
            this.imgList = res.data.message;
          }
        });
    }
  },
  mounted() {
    //图片加载
    this.getImgList(0);
    mui(".mui-scroll-wrapper").scroll({
        deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
    });
  },
  created() {
    //实现分类加载
    this.getImgCategory();
  }
};
</script>
<style lang="less" scoped>
* {
  touch-action: pan-y;
}

.photo-list {
  padding: 10px 10px 0;
  margin: 0;
  li {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
    list-style: none;
    background-color: #ccc;
    text-align: center;
    box-shadow: 0 0 5px #999;
    img {
      width: 100%;
      display: block;
    }

    img[lazy="loading"] {
      width: 40px;
      height: 300px;
      margin: auto;
    }
    .info {
      color: #fff;
      position: absolute;
      left: 0;
      bottom: 0;
      max-height: 84px;
      background-color: rgba(0, 0, 0, 0.5);
      text-align: left;
      overflow: hidden;
      .info-title {
        font-size: 14px;
      }
      .info-body {
        font-size: 12px;
      }
    }
  }
}
</style>
