<template>
    <div class="goodsDesc">
       <h3>{{goodsDesc.title}}</h3>
       <hr>
       <div v-html="goodsDesc.content" class="my-content">

       </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            id:this.$route.params.id,
            goodsDesc:{}
        }
    },
    methods:{
        getGoodsDesc(){
            this.$axios.get('http://www.liulongbin.top:3005/api/goods/getdesc/'+this.id)
            .then(res=>{
                if (res.data.status === 0) {
                    this.goodsDesc=res.data.message[0]
                    // console.log(this.goodsDesc)
                }
            })
        }
    },
    created(){
        this.getGoodsDesc()
    }
}
</script>

<style lang="less">
    .goodsDesc{
        padding: 4px;
        h3{
            font-size: 14px;
            color:#226aff;
            text-align: center;
            margin: 15px 0;
        }
        .my-content{
            font-size: 0;
            img{
                width: 100%;
                display: block;
            }
        }
    }
</style>

