<template>
    <div class="photoinfo">
        <h3>{{imgInfo.title}}</h3>
        <div class="subTitle">
            <span>发表时间:{{imgInfo.add_time|dateForat}} </span>
            <span>点击: {{imgInfo.click}}次</span>
        </div>
        <hr>
        <!-- 缩略图 -->
        <vue-preview :slides="slide1" class="my-img" ref="myImg"></vue-preview>
        <!-- 内容区域 -->
        <div class="content" v-html="imgInfo.content">
            
        </div>
        <!-- 品论区域 -->
        <comment-img :infoid="id"></comment-img>
    </div>
</template>

<script>
import Comment from '../childcomponents/Comment'


export default {
    data(){
        return {
            id:this.$route.params.id,
            imgInfo:{},
            slide1:[]
        }
    },
    methods:{
        getImgInfo(){//获取图片详情
            this.$axios.get('http://www.liulongbin.top:3005/api/getimageInfo/'+this.id)
            .then(res=>{
                // console.log(res)
                if(res.data.status === 0){
                    this.imgInfo=res.data.message[0]
                }
            })
        },
        getImg(){
            this.$axios.get('http://www.liulongbin.top:3005/api/getthumimages/'+this.id)
            .then(res=>{
                // console.log(res)
                if(res.data.status === 0){
                    res.data.message.forEach(ele => {
                        ele.msrc=ele.src
                        ele.w=600
                        ele.h=400
                    })
                   
                    this.slide1=res.data.message
                    //  console.log( this.slide1)
                }
            })
        }
    },
    created(){
        this.getImgInfo()
        this.getImg()
    },
    components:{
        'comment-img':Comment
    },
    updated(){
        // console.log(this.$refs.myImg.$el.querySelectorAll('img')[0])
        // if(this.$refs.myImg.$el.querySelectorAll('img')[0]){
        //     this.$refs.myImg.$el.querySelectorAll('figure').forEach(ele=>{
        //         ele.style.display='inline-block'
        //         ele.style.margin="1em 10px"
        //     })
        //     this.$refs.myImg.$el.querySelectorAll('img').forEach(ele=>{
        //         ele.style.width="100px"
        //     })
            
            
        // }
        
        // this.$refs.myImg.style.backGround="red"
        // document.querySelectorAll
    }
}
</script>

<style lang="less">
    .photoinfo{
        
        padding: 5px;
        h3{
            color: #26a2ff;
            font-size: 14px;
            font-weight: 600;
            text-align: center;
            margin: 15px;
        }
        .subTitle{
            display: flex;
            justify-content: space-between;
            font-size: 13px;
        }
        .content{
            font-size: 13px;
            line-height: 30px;
        }
       .my-img{
        //  background-color: red;
          img{
             width: 100px; 
            }
        figure{
            display: inline-block;
            margin:1em 10px;
        }
    }
       
        
    }
</style>
