<template>
  <div class="comment">
    <h3>发表评论</h3>
    <hr>
    <textarea placeholder="请在这里发表评论（最大bb120字）" maxlength="120" v-model="commentText"></textarea>
    <mt-button type="primary" size="large" @click="pubComment">发表评论</mt-button>

    <div class="cmt-list">
      <div class="cmt-item" v-for="(item,i) in comments" :key="i">
        <div
          class="cmt-title"
        >第{{i+1}}楼 &nbsp;&nbsp; 用户:{{item.user_name}} &nbsp;&nbsp;发表时间:{{item.add_time|dateForat}}</div>
        <div class="cmt-body">{{item.content=='undefined'?'此用户很懒...啥都没说':item.content}}</div>
      </div>
    </div>

    <mt-button type="danger" size="large" plain @click="addComment">加载更多</mt-button>
  </div>
</template>

<script>
import { Toast } from "mint-ui";

export default {
  data() {
    return {
      pageindex: 1,
      comments: [],
      commentText: ""
    };
  },
  methods: {
    getComment() {
      //获取数据列表
      this.$axios
        .get(
          "http://www.liulongbin.top:3005/api/getcomments/" +
            this.infoid +
            "?pageindex=" +
            this.pageindex
        )
        .then(res => {
          // console.log(res);
          if (res.data.status == 0) {
            // console.log(res.data.message);
            
            // this.comments = res.data.message;
            if (res.data.message.length == 0) {
              Toast("没有跟多数据了");
            }
            this.comments = this.comments.concat(res.data.message);
          } else {
            Toast("获取数据失败");
          }
        });
    },
    addComment() {
      //加载更多
      this.pageindex++;
      this.getComment();
    },
    pubComment() {
      //发表品论
      if (this.commentText.trim().length === 0) {
        Toast("品论为空");
        return;
      }

      this.$axios
        .post("http://www.liulongbin.top:3005/api/postcomment/" + this.infoid, {
          content: this.commentText
        })
        .then(
          function(res) {
            // console.log(res)
            if (res.data.status == 0) {
              let cm = {
                user_name: "匿名用户",
                add_time: Date.now(),
                content: this.commentText
              };
              this.comments.unshift(cm);
              this.commentText = "";
            }
          }.bind(this)
        );
    }
  },
  created() {
    this.getComment();
  },
  props: ["infoid"]
};
</script>
<style lang="less" scoped>
.comment {
  h3 {
    font-size: 18px;
  }
  textarea {
    height: 85px;
    margin: 0;
    font-size: 14px;
  }
  .cmt-list {
    margin: 5px 0;
    .cmt-item {
      font-size: 13px;
      .cmt-title {
        line-height: 30px;
        background-color: #ccc;
      }
      .cmt-body {
        line-height: 40px;
        text-indent: 2em;
      }
    }
  }
}
</style>

